/* Modal Here */
.modal {
  display: none;
}

.modal.active {
  display: block;
  position: fixed;
  z-index: 10;
  padding-top: 100px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  max-height: 100vh;
  overflow: auto;
  background-color: rgb(0, 0, 0);
  background-color: rgba(0, 0, 0, 0.4);
  cursor: pointer;
}

.modal-content {
  background-color: #fefefe;
  margin: 0 auto;
  border: 1px solid #888;
  max-width: 800px;
  overflow-y: auto;

  border-radius: 4px;
  position: relative;
}

.close {
  color: #fff;
  position: absolute;
  font-size: 2rem;
  left: 90%;
  top: 5%;
}

.close i:hover {
  opacity: 0.7;
}

.btn-read-more {
  border: none;
  outline: none;
  background: none;
  color: #fff;
  cursor: pointer;
  font-size: 0.9rem;
}

.btn-read-more:hover {
  opacity: 0.7;
}

@media screen and (max-width: 600px) {
  .post-title {
    font-size: 0.9rem;
  }

  .btn-read-more {
    font-size: 0.6rem;
  }

  .modal-content {
    max-width: 450px;
  }
}

